<template>
  <div>
    <el-steps :active=step align-center finish-status="success">
      <el-step title="Step 1" description="项目名称"></el-step>
      <el-step title="Step 2" description="项目申请表填写"></el-step>
      <el-step title="Step 3" description="提交项目材料"></el-step>
      <el-step title="Step 4" description="项目审核"></el-step>
    </el-steps>
    <div>
      <div style="margin: 200px;"></div>
      <el-button @click="onSubmit" v-if="show">提交项目申请</el-button>
      <el-button @click="onBack" v-else>返回主页</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResCrosAppEnd',
  data () {
    return {
      step: 3,
      show: true
    }
  },
  methods: {
    onSubmit () {
      const _this = this
      this.$axios.put(_this.GLOBAL.tarUrl + '/crossproj/submit/' + this.$store.state.proj.id)
        .then(res => {
          _this.$notify({
            title: '成功',
            message: '提交成功',
            type: 'success'
          })
          _this.step = 4
          _this.show = false
        })
    },
    onBack () {
      this.$router.push('/researchers/' + this.$store.state.username)
    }
  }
}
</script>

<style scoped>

</style>



