<template>
  <div>
    <el-table
      ref="singleTable"
      :data="tableData"
      stripe
      style="width: 100%"
      size="small"
      highlight-current-row
      @current-change="handleCurrentChange"
      id="pdfDom">
      <el-table-column
        prop="apptime"
        label="申请时间"
        align="center"
        width="240">
      </el-table-column>
      <el-table-column
        prop="name"
        label="项目名称"
        align="center"
        width="240">
      </el-table-column>
      <el-table-column
        prop="authorname"
        label="申请人"
        align="center"
        width="240">
      </el-table-column>
      <el-table-column
        prop="type"
        label="项目类型"
        align="center"
        width="240">
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态"
        align="center"
        :filters="[{ text: '未提交', value: 2}, { text: '审核中', value: 3},
        {text: '待拨款', value: 4}, {text: '已通过', value: 5}, {text: '结项申请中', value: 6}, {text: '已结项', value: 7}]"
        :filter-method="filterTag"
        filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag
            :type="setType(scope.row.status)"
            size="small"
            close-transition>{{setStatus(scope.row.status)}}</el-tag>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin: 20px">
      <el-button @click="getPdf()">导出为pdf</el-button>
    </div>
    <el-dialog
      :visible.sync="dialogVisible"
      title="项目详情">
      <!--<p v-for="(value, key) in projinfo">{{ key }}: {{ value }}</p>-->
      <AdminProjInfo></AdminProjInfo>
      <p>xiangqing</p>
    </el-dialog>
  </div>
</template>
<script>
import AdminProjInfo from './AdminProjInfo'
export default {
  comments: {
    AdminProjInfo
  },
  data () {
    return {
      tableData: this.$store.state.tableData,
      htmlTitle: this.$store.state.accountname + '\'scrossprojects',
      dialogVisible: false,
      currentRow: 0,
      projinfo: null
    }
  },
  mounted () {
    for (let i in this.tableData) {
      this.tableData[i].apptime = this.tableData[i].apptime.substr(0, 10)
      switch (this.tableData[i].type) {
        case 0:
          this.tableData[i].type = '横向项目'
          break
        case 1:
          this.tableData[i].type = '纵向项目'
          break
      }
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.currentRow = val
      // switch (this.currentRow.status) {
      //   case 0:
      //   case 1:
      //   case 3:
      //     break
      //   default:
      //     this.getProjInfo()
      //     this.dialogVisible = true
      // }
      if (this.currentRow.status >= 3) {
        this.currentRow = val
        let _this = this
        this.$store.commit('newProj', this.currentRow)
        this.$router.push('/admin/' + _this.$store.state.accountname + '/projects/' + _this.$store.state.proj.name)
      }
      console.log(this.currentRow)
    },
    setStatus (s) {
      switch (s) {
        case 3:
          return '审核中'
        case 4:
          return '待拨款'
        case 5:
          return '已通过'
        case 6:
          return '结项申请中'
        case 7:
          return '已结项'
        default:
          return '未提交'
      }
    },
    filterTag (value, row) {
      if (value === 2) {
        return row.status <= 2
      } else {
        return row.status === value
      }
    },
    setType (status) {
      switch (status) {
        case 3:
          return 'warning'
        case 4:
          return 'success'
        case 5:
          return 'info'
        case 6:
          return 'warning'
        default:
          return 'danger'
      }
    },
    getProjInfo () {
      let _this = this
      console.log('查找' + this.currentRow.id)
      this.$store.commit('newProj', this.currentRow)
      _this.$router.push('/projects/' + _this.$store.state.proj.name)
    }
  }
}
</script>



