<template>
  <div>
  <el-form id="pdfDom" label-width="80px" v-if="show" v-bind="loading">
    <el-form-item :ref="form" label="姓名" :model="form">
      <el-col :span="6">
        <el-input v-model="form.name" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="员工编号">
      <el-col :span="6">
        <el-input v-model="form.staffnum" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="身份证号">
      <el-col :span="6">
        <el-input v-model="form.id" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="生日">
      <el-col :span="5">
        <el-input v-model="form.birthday" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="TEL">
      <el-col :span="6">
        <el-input v-model="form.tel" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="Email">
      <el-col :span="10">
        <el-input v-model="form.email" width=5 :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="职称">
      <el-col :span="10">
        <el-input v-model="form.job" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="办公室">
      <el-col :span="10">
        <el-input v-model="form.office" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="擅长领域">
      <el-col :span="10">
        <el-input v-model="form.specialization" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
    <el-form-item label="住址">
        <el-col :span="10">
        <el-input v-model="form.address" :disabled="edit"></el-input>
      </el-col>
    </el-form-item>
  </el-form>
      <el-button type="primary" @click="onChange" v-if="bt1">更改</el-button>
      <el-button @click="onSubmit" v-if="bt2">提交</el-button>
      <el-button @click="onCancel">取消</el-button>
      <el-button @click="getPdf()">打印个人资料</el-button>
  </div>
</template>
<script>
// const url = 'http://localhost:8080/userinfo'
export default {
  data () {
    return {
      show: true,
      loading: true,
      form: {
        // accountid: This.$store.state.accountid,
        name: '',
        staffnum: '',
        id: '',
        birthday: '',
        tel: '',
        email: '',
        job: '',
        office: '',
        specialization: '',
        address: ''
        // accounttype: 'researcher'
      },
      bt1: true,
      bt2: false,
      edit: true,
      htmlTitle: 'userinfo',
      httpmethod: 'post'
    }
  },
  mounted () {
    console.log('get userinfo')
    // this.loadData(this.setData)
    this.getUserinfo(this.$store.state.accountid)
    this.loading = false
  },
  methods: {
    onChange () {
      this.bt1 = false
      this.bt2 = true
      this.edit = false
    },
    onSubmit () {
      console.log('submit!  method:' + this.httpmethod)
      // var router = this.$router
      this.show = false
      const This = this
      let form = this.form
      this.$axios({
        method: This.httpmethod,
        url: This.GLOBAL.tarUrl + '/userinfo',
        data: {
          accountid: This.$store.state.accountid,
          name: form.name,
          staffnum: form.staffnum,
          id: form.id,
          birthday: form.birthday,
          tel: form.tel,
          email: form.email,
          job: form.job,
          office: form.office,
          specialization: form.specialization,
          address: form.address,
          accounttype: 'researcher'
        }
      }).then(function (response) {
        console.log(response.data)
        if (response.data.code === 200) {
          This.show = true
          This.bt1 = true
          This.bt2 = false
          This.edit = true
          This.$notify({
            type: 'success',
            title: '成功',
            message: '用户信息更改成功'
          })
        }
      }).catch(function (error) {
        console.log(error)
        alert('更改失败')
      })
    },
    onCancel () {
      // this.$router.go(-1)
      this.show = false
      this.$nextTick(() => (this.show = true))
    },
    getUserinfo (id) {
      let _this = this
      this.$axios.get(_this.GLOBAL.tarUrl + '/userinfo?accountid=' + id)
        .then(function (res) {
          if (res.data.code === 200) {
            _this.form = res.data.data
            _this.httpmethod = 'put'
          }
        })
        .catch()
    }
  }
}
</script>



