<template>
    <ResItemTable :requrl="setRequrl()"></ResItemTable>
</template>

<script>
import ResItemTable from '@/pages/ResItemTable'
export default {
  name: 'ResSciItemTable',
  components: {
    ResItemTable
  },
  methods: {
    setRequrl () {
      return this.GLOBAL.tarUrl + '/item/' + this.$store.state.accountid
    }
  }
}
</script>

<style scoped>

</style>



