<template>
  <div>
    <el-table
      :data="tempData"
      stripe
      style="width: 100%"
      size="medium"
      @current-change="handleCurrentChange"
      id="pdfDom">
      <el-table-column
        prop="apptime"
        label="申请时间"
        align="center"
        width="250">
      </el-table-column>
      <el-table-column
        prop="name"
        label="项目名称"
        align="center">
      </el-table-column>
      <el-table-column
        prop="authorname"
        label="申请人"
        align="center"
        width="350">
      </el-table-column>
      <el-table-column
        prop="type"
        label="项目类型"
        align="center"
        width="240">
      </el-table-column>
      <el-table-column
        prop="status"
        label="请求"
        align="center"
        width="250"
        :filters="[{ text: '审核中', value: 3 },
        {text: '待拨款', value: 4}, {text: '结项申请中', value: 6}]"
        :filter-method="filterTag"
        filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag
            :type="setType(scope.row.status)"
            size="small"
            close-transition>{{setStatus(scope.row.status)}}</el-tag>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin: 20px">
      <el-button @click="getPdf()">导出为pdf</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AdminProjForm',
  data () {
    return {
      tempData: [],
      tableData: [],
      htmlTitle: 'projects'
    }
  },
  mounted () {
    console.log('set data')
    this.loadData2()
  },
  methods: {
    handleCurrentChange (val) {
      this.currentRow = val
      let _this = this
      this.$store.commit('newProj', this.currentRow)
      this.$router.push('/admin/' + _this.$store.state.accountname + '/projects/' + _this.$store.state.proj.name)
    },
    loadData2 () {
      let _this = this
      this.$axios.get(_this.GLOBAL.tarUrl + '/crosproj/all')
        .then(function (res) {
          _this.tableData = res.data.data
          for (let i in _this.tableData) {
            if (_this.tableData[i].status === 3 ||
              _this.tableData[i].status === 4 ||
              _this.tableData[i].status === 6) {
              _this.tempData.push(_this.tableData[i])
            }
            console.log('1')
          }
        })
    },
    setStatus (s) {
      switch (s) {
        case 3:
          return '待审核'
        case 4:
          return '拨款申请'
        case 5:
          return '已通过'
        case 6:
          return '结项申请'
        case 7:
          return '已结项'
        default:
          return '未提交'
      }
    },
    filterTag (value, row) {
      return row.status === value
    },
    setType (status) {
      switch (status) {
        case 3:
          return 'warning'
        case 4:
          return 'success'
        case 5:
          return 'info'
        case 6:
          return 'warning'
        default:
          return 'danger'
      }
    }
  }
}
</script>

<style scoped>

</style>



