<template>
  <div>
    <el-steps :active=step align-center finish-status="success">
      <el-step title="Step 1" description="纵向项目：项目名称"></el-step>
      <el-step title="Step 2" description="纵向项目：项目申请表填写"></el-step>
      <el-step title="Step 3" description="纵向项目：提交项目材料"></el-step>
      <el-step title="Step 4" description="纵向项目：项目审核"></el-step>
    </el-steps>
    <el-form label-width="170px" :model="crosAppForm">
      <el-form-item label="项目名称">
        <el-input v-model="crosAppForm.name"></el-input>
      </el-form-item>
      <el-form-item label="项目类别">
        <el-input v-model="crosAppForm.type"></el-input>
      </el-form-item>
      <el-form-item label="研究经费">
        <el-input v-model="crosAppForm.researchCost"></el-input>
      </el-form-item>
      <el-form-item label="预期经费">
        <el-input v-model="crosAppForm.expectedFounds"></el-input>
      </el-form-item>
      <el-form-item label="预期完成时间">
        <!--<el-input v-model="crosAppForm.expectedFinishTime"></el-input>-->
        <el-col style="width: 120px">
          <el-date-picker
            v-model="crosAppForm.expectedFinishTime"
            type="date"
            placeholder="选择日期"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="项目主管部门">
        <el-input v-model="crosAppForm.partner"></el-input>
      </el-form-item>
      <el-form-item size="large">
        <el-button type="primary" @click="onSubmit" v-if="bt1">提交</el-button>
        <el-button @click="onNext" v-if="bt2">下一步</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      step: 1,
      crosAppForm: {
        name: this.$store.state.proj.name
      },
      bt1: true,
      bt2: false
    }
  },
  methods: {
    onSubmit () {
      const This = this
      let myname = this.$store.state.accountname
      let id = this.$store.state.proj.id
      console.log(myname + 'projid:' + id)
      this.$axios({
        method: 'post',
        url: This.GLOBAL.tarUrl + '/crosappform',
        data: {
          projectid: id,
          name: This.crosAppForm.name,
          type: This.crosAppForm.type,
          researchCost: This.crosAppForm.researchCost,
          expectedFounds: This.crosAppForm.expectedFounds,
          expectedFinishTime: This.crosAppForm.expectedFinishTime,
          partner: This.crosAppForm.partner,
          applicant: This.$store.state.accountid
        }
      }).then(function (response) {
        console.log(response.data)
        if (response.data.code === 200) {
          This.$notify({
            title: '成功',
            message: '创建申请表成功',
            type: 'success'
          })
          This.bt1 = false
          This.bt2 = true
        } else {
          This.$notify.error({
            title: '失败',
            message: '创建申请表失败'
          })
        }
      })
        .catch(function () {
          This.$notify.error({
            title: '失败',
            message: '创建申请表失败'
          })
        })
    },
    onNext () {
      let target = '/crosapply/'
      if (this.$store.state.proj.type === 1) {
        target = '/govapply/'
      }
      this.$router.push('/researchers/' + this.$store.state.accountname + target + this.$store.state.proj.name + '/upload')
    }
  }
}
</script>



