<template>
  <div>
    <el-steps :active=step align-center finish-status="success">
      <el-step title="Step 1" description="横向项目：项目名称"></el-step>
      <el-step title="Step 2" description="横向项目：项目申请表填写"></el-step>
      <el-step title="Step 3" description="横向项目：提交项目材料"></el-step>
      <el-step title="Step 4" description="横向项目：项目审核"></el-step>
    </el-steps>
  <div style="width:100%; height:200px ; margin: 10px">
    <div style="height:30px"></div>
    <el-form :inline="true" label-width="80px">
      <el-form-item label="项目名称">
        <el-input v-model="projname"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="onNameCreate" v-if="bt1">创建</el-button>
      </el-form-item>
      <el-form-item>
        <el-button @click="onNext" v-if="bt2">下一步</el-button>
      </el-form-item>
    </el-form>
  </div>
    <!--<router-view></router-view>-->
  </div>
</template>
<script>
// import ResCrosAppForm from '@/components/ResCrosAppForm'
export default {
  data () {
    return {
      step: 0,
      projname: '',
      bt1: true,
      bt2: false
    }
  },
  // beforeRouteLeave (to, from, next) {
  //   console.log(to.name)
  //   if (to.name === 'ResCrosAppForm') {
  //     if (this.projname !== '') {
  //       // this.onNameCreate()
  //       next(true)
  //     } else {
  //       next(false)
  //     }
  //   } else {
  //     next(false)
  //   }
  // },
  methods: {
    onNameCreate () {
      const This = this
      let myname = this.$store.state.accountname
      console.log(myname)
      console.log('create' + this.projname)
      this.$axios({
        method: 'post',
        url: This.GLOBAL.tarUrl + '/crosproj',
        data: {
          // id: null,
          name: This.projname,
          authorname: myname,
          status: 0,
          type: 0
        }
      }).then(function (response) {
        console.log(response.data)
        if (response.data.code === 200) {
          // this.$router.push('')
          console.log(response.data)
          This.$store.commit('newProj', response.data.data)
          This.bt1 = false
          This.bt2 = true
        }
      })
    },
    onNext () {
      this.$router.push('/researchers/' + this.$store.state.accountname + '/crosapply/' + this.projname + '/form')
    }
  }
}
</script>



