// import Vue from 'vue'
// import Router from 'vue-router'
// import axios from 'axios'
import HelloWorld from '@/components/HelloWorld'
import ResLogin from '@/pages/ResLogin'
import ResMainPage from '@/pages/ResMainPage'
import ResInfoPage from '@/pages/ResInfoPage'
import ProjectForm from '@/components/ProjectForm'
import ResCrosAppName from '@/components/ResCrosAppName'
import ResCrosApplicant from '@/pages/ResCrosApplicant'
import ResCrosAppForm from '@/components/ResCrosAppForm'
import ResCrosAppUpload from '@/components/ResCrosAppUpload'
import ResCrosAppEnd from '@/components/ResCrosAppEnd'
import AdminMainPage from '@/pages/AdminMainPage'
import AdminProjFrom from '@/pages/admin/AdminProjForm'
import ResCrosProjDetails from '@/pages/ResCrosProjDetails'
import ResUploadItem from '@/pages/ResUploadItem'
import ResUploadSciItem from '@/pages/ResUploadSciItem'
import ResItemTable from '@/pages/ResItemTable'
import ResSciItemTable from '@/pages/ResSciItemTable'
import ResGovAppName from '@/components/ResGovAppName'
import ResGovAppForm from '@/components/ResGovAppForm'
import ProjDetails from '@/pages/admin/ProjDetails'
import ProjFormTemp from '@/pages/admin/ProjFormTemp'
import AdminSciItemTable from '@/pages/admin/AdminSciItemTable'

// Vue.use(Router)

// export default new Router({
// routes: [
export default[
  {
    path: '/HelloWorld',
    name: 'HelloWorld',
    component: HelloWorld
  },
  {
    path: '/',
    name: 'ResLogin',
    component: ResLogin
  },
  {
    path: '/test',
    name: 'ResCrosAppForm',
    component: ResCrosAppForm
  },
  {
    path: '/researchers/:id',
    name: 'ResMainPage',
    component: ResMainPage,
    children: [
      {path: '', component: ProjectForm},
      // {path: 'projects', component: ProjectForm},
      {path: 'userinfo', component: ResInfoPage},
      {
        path: 'crosapply',
        component: ResCrosApplicant,
        children: [
          {
            path: '',
            component: ResCrosAppName
          },
          {
            path: 'name',
            component: ResCrosAppName
          },
          {path: ':projname/form', component: ResCrosAppForm},
          {path: ':projname/upload', component: ResCrosAppUpload},
          {path: ':projname/submit', component: ResCrosAppEnd}
          // {path: '', component: ResCrosAppName},
        ]
      },
      {
        path: 'govapply',
        component: ResCrosApplicant,
        children: [
          {path: '', component: ResGovAppName},
          {path: ':projname/form', component: ResGovAppForm},
          {path: ':projname/upload', component: ResCrosAppUpload},
          {path: ':projname/submit', component: ResCrosAppEnd}
        ]
      },
      {path: '/projects/:projname', component: ResCrosProjDetails},
      {path: 'uploaditem', component: ResUploadSciItem},
      {path: 'getitemlist', component: ResSciItemTable}
    ]
  },
  {
    path: '/admin/:username/',
    component: AdminMainPage,
    children: [
      {path: '', component: AdminProjFrom},
      {path: 'projects/:projname', component: ProjDetails},
      {path: 'projecttable/all', component: ProjFormTemp},
      {path: 'projecttable/cros', component: ProjFormTemp},
      {path: 'projecttable/gov', component: ProjFormTemp},
      {path: 'items', component: AdminSciItemTable}
    ]
  },
  {path: '/:projname/form', component: ResCrosAppForm},
  {path: '/admin', component: AdminMainPage},
  {path: '/uploaditem', component: ResUploadItem},
  {path: '/getitemlist', component: ResItemTable},
  {path: '/govappname', component: ProjDetails}
  // {
  //   path: '/researchers/:id/userinfo',
  //   name: 'Userinfo',
  //   component: ResInfoPage
  // }
]
// })



