<template>
  <el-form label-width="200px" style="text-align: left">
    <el-form-item label="项目名称">
      <span>{{crosAppForm.name}}</span>
      <!--<el-input v-model="crosAppForm.name"></el-input>-->
    </el-form-item>
    <el-form-item label="申请人">
      <span>{{$store.state.proj.authorname}}</span>
      <!--<el-input v-model="crosAppForm.name"></el-input>-->
    </el-form-item>
    <el-form-item label="申请时间">
      <span>{{$store.state.proj.apptime}}</span>
    </el-form-item>
    <el-form-item label="项目类型">
      <!--<el-input v-model="crosAppForm.type"></el-input>-->
      <span>{{crosAppForm.type}}</span>

    </el-form-item>
    <el-form-item label="研究经费">
      <!--<el-input v-model="crosAppForm.researchCost"></el-input>-->
      <span>{{crosAppForm.researchCost}}</span>
    </el-form-item>
    <!--<el-form-item label="预期经费">-->
    <!--&lt;!&ndash;<el-input v-model="crosAppForm.expectedFounds"></el-input>&ndash;&gt;-->
    <!--<span>{{crosAppForm.expectedFounds}}</span>-->
    <!--</el-form-item>-->
    <el-form-item label="预期完成时间">
      <!--<el-input v-model="crosAppForm.expectedFinishTime"></el-input>-->
      <span>{{crosAppForm.expectedFinishTime}}</span>
    </el-form-item>
    <el-form-item :label="laName">
      <!--<el-input v-model="crosAppForm.partner"></el-input>-->
      <span>{{crosAppForm.partner}}</span>
    </el-form-item>
    <el-form-item label="状态">
      <span>{{projstatus}}</span>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'AdminProjInfo',
  data () {
    return {
      // crosAppForm: {
      //   expectedFinishTime: '2018-01-01'
      // },
      laName: '合作单位',
      projstatus: '审核中',
      show: true
    }
  },
  props: ['crosAppForm'],
  mounted () {
    this.getProjInfo()
    console.log(this.crosAppForm)
    if (this.$store.state.proj.type === 1) {
      this.laName = '项目主管部门'
    }
    if (this.$store.state.proj.status >= 6) {
      this.show = false
    }
  },
  methods: {
    getProjInfo () {
      const url = this.GLOBAL.tarUrl + '/crossproj/details/'
      const _this = this
      let id = this.$store.state.proj.id
      console.log('try to get the details of project ' + id)
      this.$axios.get(url + id)
        .then(function (res) {
          console.log(res.data.data)
          _this.crosAppForm = res.data.data
          _this.crosAppForm.expectedFinishTime = _this.crosAppForm.expectedFinishTime.substr(0, 10)
        })
    }
  }
}
</script>

<style scoped>

</style>



