<template>
  <div v-if="show">
    <el-table
      ref="singleTable"
      :data="tableData"
      v-loading="loading"
      stripe
      style="width: 100%"
      size="small"
      highlight-current-row
      @current-change="handleCurrentChange"
      id="pdfDom">
      <el-table-column
        prop="apptime"
        label="申请时间"
        align="center"
        width="240">
      </el-table-column>
      <el-table-column
        prop="name"
        label="项目名称"
        align="center"
        width="240">
      </el-table-column>
      <el-table-column
        prop="authorname"
        label="申请人"
        align="center"
        width="240">
      </el-table-column>
      <el-table-column
        prop="type"
        label="项目类型"
        align="center"
        width="240">
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态"
        align="center"
        :filters="[{ text: '未提交', value: 2 }, { text: '审核中', value: 3},
        {text: '待拨款', value: 4}, {text: '已通过', value: 5}, {text: '结项申请中', value: 6}, {text: '已结项', value: 7}]"
        :filter-method="filterTag"
        filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag
            :type="setType(scope.row.status)"
            size="small"
            close-transition>{{setStatus(scope.row.status)}}</el-tag>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin: 20px">
    <el-button @click="getPdf()">导出为pdf</el-button>
    </div>
    <el-dialog
      :visible.sync="dialogVisible"
      title="此项目申请还没获得审批，请选择操作">
      <span slot="footer" class="dialog-footer">
        <el-button type="warning" @click="dialogDelete">删除此项目</el-button>
        <el-button type="primary" @click="dialogContinue">填写并提交</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      loading: true,
      tableData: this.$store.state.tableData,
      htmlTitle: this.$store.state.accountname + '\'scrossprojects',
      dialogVisible: false,
      currentRow: 0,
      projinfo: null,
      show: true
    }
  },
  mounted () {
    for (let i in this.tableData) {
      this.tableData[i].apptime = this.tableData[i].apptime.substr(0, 10)
      switch (this.tableData[i].type) {
        case 0:
          this.tableData[i].type = '横向项目'
          break
        case 1:
          this.tableData[i].type = '纵向项目'
          break
      }
    }
    this.loading = false
  },
  methods: {
    handleCurrentChange (val) {
      this.currentRow = val
      // let url1 = this.$store.state.accountname + '/crosapply/'
      // let url2 = this.$store.state.accountname + '/govapply/'
      // let url = url1
      // if (this.currentRow.type === 1) {
      //   url = url2
      // }
      // switch (this.currentRow.status) {
      //   case 0:
      //     this.$store.commit('newProj', this.currentRow)
      //     this.$router.push(url + this.currentRow.name + '/form')
      //     break
      //   case 1:
      //     this.$store.commit('newProj', this.currentRow)
      //     this.$router.push(url + this.currentRow.name + '/upload')
      //     break
      //   case 2:
      //     this.$store.commit('newProj', this.currentRow)
      //     this.$router.push(url + this.currentRow.name + '/submit')
      //     break
      //   default:
      //     this.getProjInfo()
      //     this.dialogVisible = true
      // }
      if (this.currentRow.status <= 2) {
        this.dialogVisible = true
      } else {
        this.getProjInfo()
      }
      console.log(this.currentRow)
    },
    loadData (callBack) {
      this.getFormPromise()
        .then(res => {
          callBack(res)
        })
        .catch(res => {
          console.log(res)
        })
    },
    getFormPromise () {
      let name = this.$store.state.accountname
      let _this = this
      return new Promise((resolve, reject) => {
        this.$axios.get(_this.GLOBAL.tarUrl + '/crosproj?authorname=' + name)
          .then(res => {
            resolve(res)
          })
          .catch(res => {
            reject(res)
          })
      }
      )
    },
    setData (data) {
      this.tableData = data.data.data
      console.log(this.tableData)
      for (let i in this.tableData) {
        this.tableData[i].apptime = this.tableData[i].apptime.substr(0, 10)
        switch (this.tableData[i].type) {
          case 0:
            this.tableData[i].type = '横向项目'
            break
          case 1:
            this.tableData[i].type = '纵向项目'
            break
        }
      }
      console.log(this.tableData)
    },
    setStatus (s) {
      switch (s) {
        case 3:
          return '审核中'
        case 4:
          return '待拨款'
        case 5:
          return '已通过'
        case 6:
          return '结项申请中'
        case 7:
          return '已结项'
        default:
          return '未提交'
      }
    },
    filterTag (value, row) {
      return row.status === value
    },
    setType (status) {
      switch (status) {
        case 3:
          return 'warning'
        case 4:
          return 'success'
        case 5:
          return 'info'
        case 6:
          return 'warning'
        default:
          return 'danger'
      }
    },
    getProjInfo () {
      let _this = this
      console.log('查找' + this.currentRow.id)
      this.$store.commit('newProj', this.currentRow)
      _this.$router.push('/projects/' + _this.$store.state.proj.name)
    },
    dialogDelete () {
      let _this = this
      let id = this.currentRow.id
      // this.show = false
      this.$axios.delete(_this.GLOBAL.tarUrl + '/crosproj/' + id)
        .then(function (res) {
          // this.refreshData()
          // _this.show = true
          if (res.data.code === 200) {
            _this.$notify({
              title: '成功',
              message: '项目删除成功',
              type: 'success'
            })
          } else {
            _this.$notify({
              title: '失败',
              message: '项目删除失败',
              type: 'fail'
            })
          }
        })
        .catch(function () {
          _this.show = true
          _this.$notify({
            title: '失败',
            message: '项目删除失败',
            type: 'fail'
          })
        })
      this.dialogVisible = false
      this.refreshData()
    },
    dialogContinue () {
      let url1 = this.$store.state.accountname + '/crosapply/'
      let url2 = this.$store.state.accountname + '/govapply/'
      let url = url1
      if (this.currentRow.type === 1) {
        url = url2
      }
      switch (this.currentRow.status) {
        case 0:
          this.$store.commit('newProj', this.currentRow)
          this.$router.push(url + this.currentRow.name + '/form')
          break
        case 1:
          this.$store.commit('newProj', this.currentRow)
          this.$router.push(url + this.currentRow.name + '/upload')
          break
        case 2:
          this.$store.commit('newProj', this.currentRow)
          this.$router.push(url + this.currentRow.name + '/submit')
          break
      }
    },
    refreshData () {
      let _this = this
      this.show = false
      this.$axios.get(_this.GLOBAL.tarUrl + '/crosproj?authorname=' + _this.$store.state.accountname)
        .then(function (res) {
          if (res.data.code === 200) {
            _this.$store.commit('newTableData', res.data.data)
            _this.show = true
          }
        })
    }
  }
}
</script>



