import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    target: 'http://localhost:8088',
    accountid: 0,
    accountname: 'testuser',
    accountstatus: 0,
    proj: [],
    tableData: []
  },
  mutations: {
    newAccountid (state, msg) {
      state.accountid = msg
    },
    newAccountname (state, msg) {
      state.accountname = msg
    },
    newAccountstatus (state, msg) {
      state.accountstatus = msg
    },
    newProj (state, msg) {
      state.proj = msg
    },
    newTableData (state, msg) {
      state.tableData = msg
    }
  }
})
export default store



