<template>
  <div>
    <el-steps :active=step align-center finish-status="success">
      <el-step title="Step 1" description="项目名称"></el-step>
      <el-step title="Step 2" description="项目申请表填写"></el-step>
      <el-step title="Step 3" description="提交项目材料"></el-step>
      <el-step title="Step 4" description="项目审核"></el-step>
    </el-steps>
    <div style="margin: 100px"></div>
    <form>
      <input type="file" @change="getFile($event)">
      <el-button @click="submitForm($event)" v-if="show">提交</el-button>
      <el-button type="primary" @click="onNext" v-else>下一步</el-button>
    </form>
  </div>
</template>
<style scoped>

</style>
<script>
export default {
  data () {
    return {
      name: '',
      age: '',
      file: '',
      step: 2,
      show: true
    }
  },
  methods: {
    getFile (event) {
      this.file = event.target.files[0]
      console.log(this.file)
    },
    submitForm (event) {
      event.preventDefault()
      let formData = new FormData()
      // formData.append('name', this.name)
      formData.append('projid', this.$store.state.proj.id)
      formData.append('crossappfile', this.file)
      console.log(formData)
      let _this = this
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }

      this.$axios.post(_this.GLOBAL.tarUrl + '/crossappfile', formData, config).then(function (res) {
        console.log(res)
        _this.show = false
        // if (res.status === 2000) {
        // }
      })
    },
    onNext () {
      let target = '/crosapply/'
      if (this.$store.state.proj.type === 1) {
        target = '/govapply/'
      }
      this.$router.push('/researchers/' + this.$store.state.accountname + target + this.$store.state.proj.name + '/submit')
    }
  }
}
</script>



