<template>
  <el-container style="height: 100px; border: 1px">
    <el-header style="text-align: right; font-size: 20px; font-weight: 200; height: 80px">
      <el-button type="danger" icon="el-icon-delete" circle @click="toLogOut"></el-button>
        <span>{{this.$store.state.accountname}}</span>
      </el-header>

      <el-container>
        <el-aside
          width="300px"
          style="background-color: rgb(255, 255, 255)"
          v-if="sidebar">
          <el-menu :default-openeds="['1']">
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-message"></i>我的项目</template>
                <el-menu-item @click.native="getProjectForm" index="1-1">查看全部</el-menu-item>
            </el-submenu>
            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-menu"></i>项目申请</template>
              <el-menu-item-group>
                <el-menu-item index="2-1" @click.native="crosApplicate">横向项目申请</el-menu-item>
                <el-menu-item index="2-2" @click.native="govApplicate">纵向项目申请</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="el-icon--left"></i>
                科研成果
              </template>
              <el-menu-item @click.native="getItemList" index="3-1">查看</el-menu-item>
              <el-menu-item @click.native="upLoadItem" index="3-2">申报</el-menu-item>
            </el-submenu>
            <el-submenu index="4">
              <template slot="title">
                <i class="el-icon-setting"></i>设置</template>
                <el-menu-item @click.native="setUserInfo" index="4-1">账户信息</el-menu-item>
                <el-menu-item index="4-2">关于</el-menu-item>
            </el-submenu>
            <el-submenu index="5">
              <template slot="title">
                <i class="el-icon-bell"></i>
                高级功能
              </template>
              <!--<el-menu-item index="5-1">审核员</el-menu-item>-->
              <el-menu-item index="5-1" @click.native="adminSignin">管理员</el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <div style="width: 95%;">
            <router-view></router-view>
          </div>
        </el-main>
      </el-container>
  </el-container>
</template>

<style>
  .el-header {
    background-color: rgb(49, 145, 201);
    color: rgb(255, 255, 255);
    /* font-size: 100px; */
    line-height: 100px;
  }

  .el-aside {
    color: rgb(255, 248, 248);
  }

</style>

<script>
export default {
  data () {
    return {
      sidebar: true
    }
  },
  mounted () {
    if (this.$store.state.accountstatus === 0) {
      this.sidebar = false
      this.setUserInfo()
    }
  },
  methods: {
    toLogOut: function () {
      this.$router.push('/')
    },
    setUserInfo: function () {
      this.$router.push('/researchers/' + this.$route.params.id + '/userinfo')
    },
    getProjectForm: function () {
      let _this = this
      this.$axios.get(_this.GLOBAL.tarUrl + '/crosproj?authorname=' + _this.$store.state.accountname)
        .then(function (res) {
          if (res.data.code === 200) {
            _this.$store.commit('newTableData', res.data.data)
            _this.$router.push('/researchers/' + _this.$store.state.accountname)
          }
        })
    },
    crosApplicate: function () {
      console.log('crosapplicate')
      this.$router.push('/researchers/' + this.$route.params.id + '/crosapply')
    },
    govApplicate: function () {
      console.log('govapplicate')
      this.$router.push('/researchers/' + this.$route.params.id + '/govapply')
    },
    upLoadItem: function () {
      console.log('upload item')
      this.$router.push('/researchers/' + this.$route.params.id + '/uploaditem')
    },
    getItemList: function () {
      console.log('upload item')
      this.$router.push('/researchers/' + this.$route.params.id + '/getitemlist')
    },
    adminSignin: function () {
      this.$router.push('/admin/' + this.$store.state.accountname)
    }
  }
}

</script>



