<template>
  <el-table
    ref="singleTable"
    :data="tableData"
    stripe
    style="width: 100%"
    size="small"
    highlight-current-row
    @current-change="handleCurrentChange"
    id="pdfDom">
    <el-table-column
      prop="type"
      label="项目类别"
      align="center"
      :filters="[{ text: '论文', value: 1 }, { text: '专著', value: 2 }, { text: '获奖', value: 3 }]"
      :filter-method="filterTag"
      width="240"
      filter-placement="bottom-end">
      <template slot-scope="scope">
        <el-tag
          :type="tagGen(scope.row.type)"
          size="small"
          close-transition>{{setStatus(scope.row.type)}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      prop="name"
      label="成果名称"
      align="center">
    </el-table-column>
    <el-table-column
      prop="accountname"
      label="提交者"
      align="center">
    </el-table-column>
    <el-table-column
      prop="projectname"
      label="所属项目"
      align="center">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  data () {
    return {
      tableData: [
        {type: 1, name: 'hello'}
      ],
      currentRow: 0
    }
  },
  props: ['requrl'],
  mounted () {
    console.log(this.requrl)
    this.tableInit()
  },
  methods: {
    handleCurrentChange (val) {
      this.currentRow = val
      this.getItemFile(this.currentRow.id)
    },
    tagGen (t) {
      let tagtype = 'danger'
      switch (t) {
        case 1:
          tagtype = 'success'
          break
        case 2:
          tagtype = 'info'
          break
        default:
          tagtype = 'danger'
      }
      return tagtype
    },
    getItemFile (id) {
      console.log(id)
      window.location = this.GLOBAL.tarUrl + '/item/file/' + id
    },
    tableInit () {
      let _this = this
      this.$axios.get(this.requrl)
        .then(function (res) {
          if (res.data.code === 200) {
            _this.tableData = res.data.data
          }
          console.log(typeof (res.data.data.data))
          console.log(_this.tableData)
        })
    },
    filterTag (value, row) {
      return row.type === value
    },
    setStatus (s) {
      switch (s) {
        case 1:
          return '论文'
        case 2:
          return '专著'
        default:
          return '获奖'
      }
    }
  }
}
</script>

<style scoped>

</style>



