<template>
  <div>
    <el-form label-width="200px" style="text-align: left">
      <el-form-item label="项目名称">
        <span>{{crosAppForm.name}}</span>
        <!--<el-input v-model="crosAppForm.name"></el-input>-->
      </el-form-item>
      <el-form-item label="申请人">
        <span>{{$store.state.proj.authorname}}</span>
        <!--<el-input v-model="crosAppForm.name"></el-input>-->
      </el-form-item>
      <el-form-item label="申请时间">
        <span>{{$store.state.proj.apptime}}</span>
      </el-form-item>
      <el-form-item label="项目类型">
        <!--<el-input v-model="crosAppForm.type"></el-input>-->
        <span>{{crosAppForm.type}}</span>

      </el-form-item>
      <el-form-item label="研究经费">
        <!--<el-input v-model="crosAppForm.researchCost"></el-input>-->
        <span>{{crosAppForm.researchCost}}</span>
      </el-form-item>
      <!--<el-form-item label="预期经费">-->
        <!--&lt;!&ndash;<el-input v-model="crosAppForm.expectedFounds"></el-input>&ndash;&gt;-->
        <!--<span>{{crosAppForm.expectedFounds}}</span>-->
      <!--</el-form-item>-->
      <el-form-item label="预期完成时间">
        <!--<el-input v-model="crosAppForm.expectedFinishTime"></el-input>-->
        <span>{{crosAppForm.expectedFinishTime}}</span>
      </el-form-item>
      <el-form-item :label="laName">
        <!--<el-input v-model="crosAppForm.partner"></el-input>-->
        <span>{{crosAppForm.partner}}</span>
      </el-form-item>
      <el-form-item label="状态">
        <span>{{projstatus}}</span>
      </el-form-item>
    </el-form>
    <div style="text-align: left;">
      <el-dialog
        :visible.sync="dialogVisible"
        title="添加科研成果">
      <!--<span slot="footer" class="dialog-footer">-->
        <!--<el-button type="warning" @click="dialogDelete">删除此项目</el-button>-->
        <!--<el-button type="primary" @click="dialogContinue">填写并提交</el-button>-->
      <!--</span>-->
        <div style="width: 350px; left: 50%; right: 50%; transform: translate(+20%,+0%);">
          <ResUploadItem
            :projectid="$store.state.proj.id"
            :projectname="$store.state.proj.name">
          </ResUploadItem>
        </div>
      </el-dialog>
      <div style="margin-top: 100px; margin-left: 40%">
        <el-button type="primary" size="small" @click="getProjFile">下载申请书</el-button>
        <el-button type="warning" size="small" @click="addItem" v-if="addshow">添加科研成果</el-button>
        <el-button type="danger" size="small" @click="deleteProj" v-if="delButton">删除该项目</el-button>
        <el-button type="danger" size="small" @click="appFinish" v-if="show">申请结题</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import ResUploadItem from '@/pages/ResUploadItem'
export default {
  name: 'ResCrosProjDetails',
  data () {
    return {
      crosAppForm: {
        expectedFinishTime: '2018-01-01'
      },
      laName: '合作单位',
      projstatus: '审核中',
      dialogVisible: false,
      addshow: false,
      delButton: false,
      show: true
    }
  },
  components: {
    ResUploadItem
  },
  mounted () {
    this.getProjInfo()
    console.log(this.crosAppForm)
    if (this.$store.state.proj.type === 1) {
      this.laName = '项目主管部门'
    }
    if (this.$store.state.proj.status <= 3) {
      this.delButton = true
    }
    if (this.$store.state.proj.status > 3) {
      this.addshow = true
    }
    if (this.$store.state.proj.status >= 6) {
      this.show = false
    }
  },
  methods: {
    getProjInfo () {
      const url = this.GLOBAL.tarUrl + '/crossproj/details/'
      const _this = this
      let id = this.$store.state.proj.id
      console.log('try to get the details of project ' + id)
      this.$axios.get(url + id)
        .then(function (res) {
          console.log(res.data.data)
          _this.crosAppForm = res.data.data
          _this.crosAppForm.expectedFinishTime = _this.crosAppForm.expectedFinishTime.substr(0, 10)
        })
    },
    getProjFile () {
      const url = this.GLOBAL.tarUrl + '/crossappfile/'
      window.location = url + this.$store.state.proj.id
    },
    addItem () {
      this.dialogVisible = true
    },
    deleteProj () {
      let _this = this
      this.$axios.delete(_this.GLOBAL.tarUrl + '/crosproj/' + _this.$store.state.proj.id)
        .then(function (res) {
          console.log(res)
          _this.$notify({
            title: '成功',
            message: '删除成功',
            type: 'success'
          })
          _this.$router.go(-1)
        })
    },
    appFinish () {
      let _this = this
      this.$axios.put(_this.GLOBAL.tarUrl + '/crosproj/' + _this.$store.state.proj.id + '/status/6')
        .then(function (res) {
          if (res.data.code === 200) {
            _this.$notify({
              title: '成功',
              message: '已经成功提交结题申请',
              type: 'success'
            })
          }
        })
    }
  }
}
</script>

<style scoped>

</style>



