<template>
  <el-form label-width="100px">
    <el-form-item label="名称">
      <el-input v-model="name">名称</el-input>
    </el-form-item>
    <el-form-item label="类别">
      <el-radio-group v-model="radio1">
          <el-radio-button label="论文"></el-radio-button>
          <el-radio-button label="专著"></el-radio-button>
          <el-radio-button label="获奖"></el-radio-button>
        </el-radio-group>
    </el-form-item>
    <el-form-item label="文件上传">
      <input type="file" @change="getFile($event)">
      <el-button @click="submitForm($event)">提交</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'ResUploadItem',
  data () {
    return {
      radio1: '论文',
      name: '',
      show: true
    }
  },
  props: ['projectid', 'projectname'],
  mounted () {
    console.log(this.projectid + this.projectname)
  },
  methods: {
    getFile (event) {
      this.file = event.target.files[0]
      console.log(this.file)
    },
    submitForm (event) {
      event.preventDefault()
      let formData = new FormData()
      formData.append('name', this.name)
      formData.append('accountid', this.$store.state.accountid)
      formData.append('itemfile', this.file)
      formData.append('projectid', this.projectid)
      formData.append('projectname', this.projectname)
      formData.append('accountname', this.$store.state.accountname)
      let type = 1
      switch (this.radio1) {
        case '论文':
          type = 1
          break
        case '专著':
          type = 2
          break
        case '获奖':
          type = 3
          break
      }
      formData.append('type', type)
      console.log(formData)
      let _this = this
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      this.$axios.post(_this.GLOBAL.tarUrl + '/item', formData, config).then(function (res) {
        console.log(res)
        _this.$notify({
          title: '成功',
          message: '提交成功',
          type: 'success'
        })
      })
    }
  }
}
</script>

<style scoped>

</style>



