<template>
  <el-container>
    <el-header style="background-color: white; height: 40px">
    <el-menu
      :default-active="activeIndex2"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-menu-item index="1" @click="showIssues">Issues</el-menu-item>
      <el-submenu index="2">
        <template slot="title">科研项目</template>
        <el-menu-item index="2-1" @click="getAllProjects">所有项目</el-menu-item>
        <el-menu-item index="2-2" @click="getCrosProjects">横向项目</el-menu-item>
        <el-menu-item index="2-3" @click="getGovProjects">纵向项目</el-menu-item>
      </el-submenu>
      <el-menu-item index="3" @click="getItems">科研成果管理</el-menu-item>
      <!--<el-menu-item index="4">用户管理</el-menu-item>-->
    </el-menu>
    </el-header>
    <el-header style="text-align: right; font-size: 40px; background-color: white; height: 55px">
      <el-dropdown>
      <i class="el-icon-setting" style="margin-right: 20px; color: black">{{this.$store.state.accountname}}</i>
      <el-dropdown-menu slot="dropdown">
      <el-dropdown-item @click.native="toLogOut">退出</el-dropdown-item>
      </el-dropdown-menu>
      </el-dropdown>
    </el-header>
      <el-main>
        <router-view v-if="show"></router-view>
      </el-main>
    </el-container>
  <!--</el-container>-->
  <!--</el-container>-->
</template>

<script>
export default {
  data () {
    return {
      show: true
    }
  },
  name: 'AdminMainPage',
  methods: {
    showIssues () {
      this.$router.push('/admin/' + this.$store.state.accountname)
    },
    toLogOut () {
      this.$router.push('/')
    },
    getAllProjects () {
      // this.$router.push('/admin' + this.$store.state.accountname + '/projects/all')
      this.getProjectsAndPush(2, 'all')
    },
    getCrosProjects () {
      this.getProjectsAndPush(0, 'cros')
    },
    getGovProjects () {
      this.getProjectsAndPush(1, 'gov')
    },
    /*
    type:0,1,2
    dest:all\cros\gov
    * */
    getProjectsAndPush (type, dest) {
      this.show = false
      let _this = this
      this.$axios.get(_this.GLOBAL.tarUrl + '/crosproj/type/' + type)
        .then(function (res) {
          if (res.data.code === 200) {
            _this.$store.commit('newTableData', res.data.data)
            _this.show = true
            console.log(_this.$store.state.tableData)
            _this.$router.push('/admin/' + _this.$store.state.accountname + '/projecttable/' + dest)
          }
        })
    },
    getItems () {
      this.$router.push('/admin/' + this.$store.state.accountname + '/items')
    }
  }
}
</script>

<style scoped>

</style>



