<template>
  <div>
    <el-form label-width="200px" style="text-align: left">
      <el-form-item label="项目名称">
        <span>{{crosAppForm.name}}</span>
      </el-form-item>
      <el-form-item label="申请人">
        <span>{{$store.state.proj.authorname}}</span>
      </el-form-item>
      <el-form-item label="申请时间">
        <span>{{$store.state.proj.apptime}}</span>
      </el-form-item>
      <el-form-item label="项目类型">
        <span>{{crosAppForm.type}}</span>
      </el-form-item>
      <el-form-item label="研究经费">
        <span>{{crosAppForm.researchCost}}</span>
      </el-form-item>
      <el-form-item label="预期完成时间">
        <span>{{crosAppForm.expectedFinishTime}}</span>
      </el-form-item>
      <el-form-item :label="laName">
        <span>{{crosAppForm.partner}}</span>
      </el-form-item>
      <el-form-item label="状态">
        <span>{{projstatus}}</span>
      </el-form-item>
    </el-form>
    <div style="text-align: left;">
      <el-form label-width="200px">
        <el-form-item label="申请">
          <span>{{requirement}}</span>
        </el-form-item>
      </el-form>
      <div style="margin-top: 100px; margin-left: 40%">
        <el-button type="primary" size="small" @click="getProjFile">下载申请书</el-button>
        <el-button type="primary" size="small" @click="button1" v-if="bt1">同意立项</el-button>
        <el-button type="danger" size="small" @click="button2" v-if="bt2">确定拨款</el-button>
        <el-button type="danger" size="small" @click="button3" v-if="bt3">允许结题</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      crosAppForm: {
        expectedFinishTime: '2018-01-01'
      },
      laName: '合作单位',
      projstatus: '审核中',
      bt1: false,
      bt2: false,
      bt3: false,
      requirement: null
      // requirement: '无'
    }
  },
  mounted () {
    console.log(this.$store.state.proj.status)
    this.getProjInfo()
    this.init(this.$store.state.proj.status)
  },
  methods: {
    init (s) {
      switch (s) {
        case 3:
          this.requirement = '申请立项'
          this.bt1 = true
          break
        case 4:
          this.requirement = '申请拨款'
          this.bt2 = true
          break
        case 6:
          this.requirement = '申请结项'
          this.bt3 = true
          break
      }
    },
    setAllFalse () {
      this.bt1 = false
      this.bt2 = false
      this.bt3 = false
    },
    button1 () {
      let _this = this
      this.$axios.put(_this.GLOBAL.tarUrl + '/crosproj/' + _this.$store.state.proj.id + '/status/4')
        .then(function (res) {
          if (res.data.code === 200) {
            _this.setAllFalse()
            _this.$notify({
              title: '成功',
              message: '已经成功通过立项申请',
              type: 'success'
            })
          }
        })
    },
    button2 () {
      let _this = this
      this.$axios.put(_this.GLOBAL.tarUrl + '/crosproj/' + _this.$store.state.proj.id + '/status/5')
        .then(function (res) {
          if (res.data.code === 200) {
            _this.setAllFalse()
            _this.$notify({
              title: '成功',
              message: '已经成功拨款',
              type: 'success'
            })
          }
        })
    },
    button3 () {
      let _this = this
      this.$axios.put(_this.GLOBAL.tarUrl + '/crosproj/' + _this.$store.state.proj.id + '/status/7')
        .then(function (res) {
          if (res.data.code === 200) {
            _this.setAllFalse()
            _this.$notify({
              title: '成功',
              message: '已经成功通过结题申请',
              type: 'success'
            })
          }
        })
    },
    getProjInfo () {
      const url = this.GLOBAL.tarUrl + '/crossproj/details/'
      const _this = this
      let id = this.$store.state.proj.id
      console.log('try to get the details of project ' + id)
      this.$axios.get(url + id)
        .then(function (res) {
          console.log(res.data.data)
          _this.crosAppForm = res.data.data
          _this.crosAppForm.expectedFinishTime = _this.crosAppForm.expectedFinishTime.substr(0, 10)
        })
    },
    getProjFile () {
      const url = this.GLOBAL.tarUrl + '/crossappfile/'
      window.location = url + this.$store.state.proj.id
    }
  }
}
</script>

<style scoped>

</style>



