<template id="resLoginTemp">
  <div style="margin: 60px">
  <div class="module form-module">
    <div>
      <el-tabs type="border-card">
        <el-tab-pane label="登陆">
          <div style="margin: 20px; margin-right: 40px">
            <el-form label-width="80px" >
              <el-form-item  label="用户名">
                <!--@keyup.enter.native监听回车，赢定要有.native-->
                <el-input v-model="signin.username" @keyup.enter.native="onSignin"></el-input>
              </el-form-item>
              <el-form-item  label="密码">
                <el-input type="password" v-model="signin.password" @keyup.enter.native="onSignin"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSignin">登陆</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
        <el-tab-pane label="注册" v-if="show">
          <div style="margin: 20px; margin-right: 40px">
            <el-form :model="signup" :rules="rules" ref="signup" label-width="80px" >
              <el-form-item  label="用户名" prop="username">
                <el-input v-model="signup.username" @keyup.enter.native="onSignup"></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="passwd1">
                <el-input type="password" v-model="signup.passwd1" @keyup.enter.native="onSignup"></el-input>
              </el-form-item>
              <el-form-item label="密码确认" prop="passwd2">
                <el-input type="password" v-model="signup.passwd2" @keyup.enter.native="onSignup"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSignup">注册</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
  </div>
</template>
<script>
module.exports = {
  data: function () {
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.signup.passwd2 !== '') {
          this.$refs.signup.validateField('passwd2')
        }
        callback()
      }
    }
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入确认密码'))
      } else if (value !== this.signup.passwd1) {
        callback(new Error('密码不一致'))
      } else {
        callback()
      }
    }
    return {
      signin: {
        username: '',
        passwd: ''
      },
      signup: {
        username: '',
        passwd1: '',
        passwd2: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 15, message: '长度在3到15之间', trigger: 'blur' }
        ],
        passwd1: [
          { validator: validatePass, trigger: 'blur' },
          { min: 6, max: 15, message: '长度在6到15之间', trigger: 'blur' }
        ],
        passwd2: [
          { validator: validatePass2, trigger: 'blur' },
          { min: 6, max: 15, message: '长度在6到15之间', trigger: 'blur' }
        ]
      },
      show: true
    }
  },
  methods: {
    onSignin: function () {
      // let router = this.$router
      let store = this.$store
      let signin = this.signin
      let _this = this
      this.$axios({
        method: 'post',
        url: _this.$store.state.target + '/account/signin',
        data: {
          id: null,
          accountname: signin.username,
          passwd: signin.password,
          regtime: null
        }
      }).then(function (response) {
        console.log(response.data)
        console.log(response.data)
        if (response.data.code === 200) {
          _this.$store.commit('newAccountid', response.data.data.id) // 存储用户id
          _this.$store.commit('newAccountname', response.data.data.accountname) // 存储用户名称
          _this.$store.commit('newAccountstatus', response.data.data.status) // 存储用户状态0:注册，未填写用户信息；1：注册并已经填写用户信息
          _this.$message({
            message: '欢迎回来，' + _this.signin.username,
            type: 'success'
          })
          _this.initAndJump()
          console.log(store.state.accountid + store.state.accountname)
        } else {
          _this.$message.error('请检查用户名或密码')
        }
      })
        .catch(function (error) {
          _this.$message.error('服务器异常')
          console.log(error)
        })
    },
    onSignup: function () {
      if (this.signup.username === '') {
        this.$message.error('用户名不能为空')
      } else if (this.signup.passwd1 === '' || this.signup.passwd2 === '') {
        this.$message.error('密码不能为空')
      } else if (this.signup.passwd1 !== this.signup.passwd2) {
        this.$message.error('密码不一致')
      } else if (this.signup.passwd1.length < 6 || this.signup.passwd1.length > 15) {
        this.$message.error('密码长度在6到15之间')
      } else {
        this.createAccount()
      }
    },
    createAccount: function () {
      let _this = this
      let signup = this.signup
      this.$axios({
        method: 'post',
        url: _this.GLOBAL.tarUrl + '/account/signup',
        data: {
          id: null,
          accountname: signup.username,
          passwd: signup.passwd1,
          regtime: null,
          status: 0
        }
      }).then(function (response) {
        console.log(response.data)
        console.log(response.data)
        if (response.data.code === 200) {
          _this.accountname = ''
          _this.passwd = ''
          _this.signup.username = ''
          _this.signup.passwd1 = ''
          _this.signup.passwd2 = ''
          _this.$message({
            message: '账户注册成功',
            type: 'success'
          })
        } else {
          // _this.show = false
          _this.$message({
            message: '账户注册失败，用户名已经存在',
            type: 'error'
          })
        }
        // _this.show = true
      })
        .catch(function (error) {
          _this.show = true
          console.log(error)
          _this.$message({
            message: '账户注册失败，请检查网络连接',
            type: 'error'
          })
        })
    },
    /*
    *存储项目表
    */
    initAndJump () {
      let _this = this
      this.$axios.get(_this.GLOBAL.tarUrl + '/crosproj?authorname=' + _this.$store.state.accountname)
        .then(function (res) {
          if (res.data.code === 200) {
            _this.$store.commit('newTableData', res.data.data)
            console.log(_this.$store.state.tableData)
            _this.$router.push('/researchers/' + _this.signin.username)
          }
        })
    }
  }
}
</script>

<style scoped>
  .form-module {
    position: relative;
    background: #ffffff;
    width: 400px;
    border-top: 2px solid #33b5e5;
    -webkit-box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
  }
</style>



